.book-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    
  }
  
  .book {
    border: 1px solid red;
    padding: 20px;
    width: 90%;
    height: 100%;
    background-color: #171414;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  }
  
   
  .title {
    font-size: 90px;
    color: white;
    font-family: monospace;
    text-align: center;
  }
  
  .book-content {
    font-size: 10px;
    color: white;
    text-align: left;
    font-family: monospace;
  }
  